<template>
  <div class="card">
    <div class="card-body">
      <h3 class="text-dark">
        <strong>Ubah Data Orang Tua/Wali</strong>
      </h3>
      <br />
      <form @submit.prevent="submit">
        <div class="form-group">
          <label for="inputNameFather">Nama Ayah Kandung</label>
          <input
            type="text"
            class="form-control"
            id="inputNameFather"
            placeholder="Nama Ayah Kandung"
            v-model="form.father_full_name"
            required
          />
        </div>
        <div class="form-group">
          <label for="inputTTLfather">Tanggal Lahir Ayah</label>
          <input
            type="date"
            class="form-control"
            id="inputTTLfather"
            placeholder="Tanggal Lahir Ayah"
            v-model="form.father_date_of_birth"
            required
          />
        </div>
        <div class="form-group">
          <label for="pddkayah">Pendidikan Ayah</label>
          <select
            v-model="form.father_education_level_id"
            class="form-control"
            id="pddkayah"
            required
          >
            <option
              :value="null"
              v-if="getDataFather.education_level_id == null"
            >
              Pilih Pendidikan Terakhir Ayah
            </option>
            <option selected :value="getDataFather.education_level_id" disabled v-else>
              {{ getDataFather.education_level }}
            </option>
            <option
              v-for="pendidikan in getDataPendidikan"
              :key="pendidikan.education_level_id"
              :value="pendidikan.education_level_id"
            >
              {{ pendidikan.education_level }}
            </option>
          </select>
        </div>
        <div class="form-group">
          <label for="pkjayah">Pekerjaan Ayah</label>
          <select
            v-model="form.father_job_field_id"
            class="form-control"
            id="pkjayah"
            required
          >
            <option
              :value="getDataFather.job_field_id"
              v-if="getDataFather.job_field_id != null"
            >
              {{ getDataFather.job_field }}
            </option>
            <option
              :value="null"
              disabled
              v-else-if="getDataFather.job_field_id == null"
            >
              Pilih Pekerjaan Ayah
            </option>

            <option
              v-for="pekerjaan in getDataPekerjaan"
              :key="pekerjaan.job_field_id"
              :value="pekerjaan.job_field_id"
            >
              {{ pekerjaan.job_field }}
            </option>
          </select>
        </div>
        <div class="form-group">
          <label for="phasilayah">Penghasilan Ayah</label>
          <select
            class="form-control"
            id="phasilayah"
            v-model="form.father_salary_id"
            required
          >
            <option
              :value="null"
              disabled
              v-if="getDataFather.salary_range_id === null"
            >
              Pilih Penghasilan Ayah
            </option>
            <option :value="getDataFather.salary_id" disabled v-else>
              {{ getDataFather.salary_range }}
            </option>
            <option
              v-for="penghasilan in getDataPenghasilan"
              :key="penghasilan.salary_id"
              :value="penghasilan.salary_id"
            >
              {{ penghasilan.salary_range }}
            </option>
          </select>
        </div>
        <div class="form-group">
          <label for="nmibu"
            >Nama Ibu Kandung <span class="text-danger">*</span></label
          >
          <input
            type="text"
            class="form-control"
            id="nmibu"
            placeholder="Nama Ibu Kandung"
            v-model="form.mother_full_name"
            required
          />
        </div>
        <div class="form-group">
          <label for="tglibu">Tanggal Lahir Ibu</label>
          <input
            type="date"
            class="form-control"
            id="tglibu"
            placeholder="Tanggal Lahir Ibu"
            v-model="form.mother_date_of_birth"
          />
        </div>
        <div class="form-group">
          <label for="pddkibu">Pendidikan Ibu</label>
          <select
            v-model="form.mother_education_level_id"
            class="form-control"
            id="pddkibu"
            required
          >
            <option
              value="null"
              v-if="getDataMother.education_level_id == null"
              disabled
            >
              Pilih Pendidikan Terakhir
            </option>
            <option v-else :value="getDataMother.education_level_id" disabled>
              {{ getDataMother.education_level }}
            </option>
            <option
              v-for="pendidikan in getDataPendidikan"
              :key="pendidikan.education_level_id"
              :value="pendidikan.education_level_id"
            >
              {{ pendidikan.education_level }}
            </option>
          </select>
        </div>
        <div class="form-group">
          <label for="pkjibu">Pekerjaan Ibu</label>
          <select
            v-model="form.mother_job_field_id"
            class="form-control"
            id="pkjibu"
            required
          >
            <option
              value="null"
              v-if="getDataMother.job_field_id === null"
              disabled
            >
              Pilih Pekerjaan Ibu
            </option>
            <option :value="getDataMother.job_field_id" v-else>
              {{ getDataMother.job_field }}
            </option>
            <option
              v-for="pekerjaan in getDataPekerjaan"
              :key="pekerjaan.job_field_id"
              :value="pekerjaan.job_field_id"
            >
              {{ pekerjaan.job_field }}
            </option>
          </select>
        </div>
        <div class="form-group">
          <label for="phasilibu">Penghasilan Ibu</label>

          <select
            v-model="form.mother_salary_id"
            class="form-control"
            id="phasilibu"
          >
            <option
              value="null"
              disabled
              v-if="getDataMother.salary_range_id === null"
            >
              Pilih Penghasilan Ibu
            </option>
            <option :value="getDataMother.salary_id" disabled v-else>
              {{ getDataMother.salary_range }}
            </option>
            <option
              v-for="penghasilan in getDataPenghasilan"
              :key="penghasilan.salary_id"
              :value="penghasilan.salary_id"
            >
              {{ penghasilan.salary_range }}
            </option>
          </select>
        </div>
        <div class="form-row">
          <div class="form-group col-md-6">
            <label for="anak">Anak Ke</label>
            <input
              type="text"
              class="form-control"
              id="anak"
              placeholder="Anak Ke"
              v-model="form.number_of_sibling"
            />
          </div>
          <div class="form-group col-md-6">
            <label for="dari">Dari....Bersaudara</label>
            <input
              type="text"
              class="form-control"
              id="dari"
              placeholder="Dari....Bersaudara"
              v-model="form.order_child"
            />
          </div>
        </div>
        <div class="p-2 mb-2 bg-biru-accent text-dark">
          *Isi kolom identitas wali berikut jika ada.
        </div>
        <div class="form-group">
          <label for="nmwali">Nama Wali</label>
          <input
            type="text"
            class="form-control"
            id="nmwali"
            placeholder="Nama Wali"
            v-model="form.guardian_full_name"
          />
        </div>
        <div class="form-group">
          <label for="pkjwali">Pekerjaan Wali</label>
          <select
            class="form-control"
            id="pkjwali"
            v-model="form.guardian_job_field_id"
            required
          >
            <option
              value="null"
              disabled
              v-if="getDataGUardian.job_field_id === null"
            >
              Pilih Pekerjaan Wali
            </option>
            <option :value="getDataGUardian.job_field_id" disabled v-else>
              {{ getDataGUardian.job_field }}
            </option>
            <option
              v-for="pekerjaan in getDataPekerjaan"
              :key="pekerjaan.job_field_id"
              :value="pekerjaan.job_field_id"
            >
              {{ pekerjaan.job_field }}
            </option>
          </select>
        </div>
        <!-- <div class="form-group">
          <label for="hbngankekerabatan">Hubungan Kekerabatan</label>
          <input
            type="text"
            class="form-control"
            id="hbngankekerabatan"
            placeholder="Hubungan Kekerabatan"
            v-model="form.guardi"
          />
        </div> -->
        <div class="form-group">
          <label for="almttl">Alamat Tinggal</label>
          <textarea
            type="text"
            class="form-control"
            id="almttl"
            placeholder="Alamat Tinggal"
            v-model="form.guardian_address"
          />
        </div>
        <div class="form-row">
          <div class="form-group col-md-6">
            <label for="inputProv">Provinsi</label>
            <select
              id="inputProv"
              class="form-control"
              v-model="form.guardian_province_id"
              required
            >
              <option
                :value="null"
                disabled
                v-if="getDataGUardian.province_id === null"
              >
                Pilih Provinsi
              </option>
              <option :value="getDataGUardian.province_id" disabled v-else>
                {{ getDataGUardian.province }}
              </option>
              <option
                v-for="provinsi in getDataProvinsi"
                :key="provinsi.province_id"
                :value="provinsi.province_id"
              >
                {{ provinsi.province }}
              </option>
            </select>
          </div>
          <div class="form-group col-md-6">
            <label for="inputKokab">Kota / Kabupaten</label>
            <select
              id="inputKokab"
              class="form-control"
              v-model="form.guardian_city_id"
            >
              <option
                :value="null"
                disabled
                v-if="getDataGUardian.city_id === null"
              >
                Pilih Kota / Kabupaten
              </option>

              <option :value="getDataGUardian.city_id" disabled v-else>
                {{ getDataGUardian.city }}
              </option>

              <option
                v-for="kota in getDataKota"
                :key="kota.city_id"
                :value="kota.city_id"
              >
                {{ kota.city }}
              </option>
            </select>
          </div>
        </div>
        <div class="form-row">
          <div class="form-group col-md-6">
            <label for="inputKec">Kecamatan</label>
            <select
              id="inputKec"
              class="form-control"
              v-model="form.guardian_district_id"
            >
              <option
                :value="null"
                disabled
                v-if="getDataGUardian.district_id === null"
              >
                Pilih Kecamatan
              </option>

              <option :value="getDataGUardian.district_id" disabled v-else>
                {{ getDataGUardian.district }}
              </option>

              <option
                v-for="kecamatan in getDataKecamatan"
                :key="kecamatan.district_id"
                :value="kecamatan.district"
              >
                {{ kecamatan.district }}
              </option>
            </select>
          </div>
          <div class="form-group col-md-6">
            <label for="inputDesa">Desa/Kelurahan</label>
            <select
              id="inputDesa"
              class="form-control"
              v-model="form.guardian_village_id"
            >
              <option
                :value="null"
                disabled
                v-if="getDataGUardian.village_id === null"
              >
                Pilih Desa / Kelurahan
              </option>

              <option :value="getDataGUardian.village_id" disabled v-else>
                {{ getDataGUardian.village }}
              </option>

              <option
                v-for="desa in getDataDesa"
                :key="desa.village_id"
                :value="desa.village_id"
              >
                {{ desa.village }}
              </option>
            </select>
          </div>
        </div>
        <div class="form-row">
          <div class="form-group col-md-6">
            <div class="form-row">
              <div class="from-group col-md-6">
                <label for="inputRt">RT</label>
                <input
                  type="text"
                  class="form-control"
                  id="inputRt"
                  placeholder="RT"
                  v-model="form.guardian_rt"
                />
              </div>
              <div class="from-group col-md-6">
                <label for="inputRw">RW</label>
                <input
                  type="text"
                  class="form-control"
                  id="inputRw"
                  placeholder="RW"
                  v-model="form.guardian_rw"
                />
              </div>
            </div>
          </div>
          <div class="form-group col-md-6">
            <label for="inputPostal">Kode Pos</label>
            <input
              type="text"
              class="form-control"
              id="inputPostal"
              placeholder="Kode Pos"
            />
          </div>
        </div>
        <div class="form-row">
          <div class="form-group col-md-6">
            <label for="inputTlp">Telepon</label>
            <input
              type="text"
              class="form-control"
              id="inputTlp"
              placeholder="Telepon"
              v-model="form.guardian_telephone_number"
            />
          </div>
          <div class="form-group col-md-6">
            <label for="inputHp">Nomor HP</label>
            <input
              type="text"
              class="form-control"
              id="inputHp"
              placeholder="Nomor HP"
              v-model="form.guardian_handphone_number"
            />
          </div>
        </div>
        <button type="submit" class="btn btn-primary btn-success">
          SIMPAN
        </button>
      </form>
    </div>
  </div>
</template>

<script>
import axios from "axios";
import { mapActions, mapGetters } from "vuex";
export default {
  name: "DataOrtu",
  inject:['notyf'],
  data() {
    return {
      pendidikan: [],
      pekerjaan: [],
      penghasilan: [],
      provinsi: [],
      kecamatan: [],
      kota: [],
      desa: [],
      form: {
        npm: null,
        father_full_name: null,
        father_date_of_birth: null,
        father_education_level_id: null,
        father_job_field_id: null,
        father_salary_id: null,
        father_handphone_number: null,
        father_telephone_number: null,
        father_address: null,
        father_province_id: null,
        father_city_id: null,
        father_district_id: null,
        father_village_id: null,
        father_rt: null,
        father_rw: null,
        father_postal: null,
        mother_full_name: null,
        mother_date_of_birth: null,
        mother_education_level_id: null,
        mother_job_field_id: null,
        mother_salary_id: null,
        mother_handphone_number: null,
        mother_telephone_number: null,
        mother_address: null,
        mother_province_id: null,
        mother_city_id: null,
        mother_district_id: null,
        mother_village_id: null,
        mother_rt: null,
        mother_rw: null,
        mother_postal: null,
        guardian_full_name: null,
        guardian_date_of_birth: null,
        guardian_education_level_id: null,
        guardian_job_field_id: null,
        guardian_salary_id: null,
        guardian_handphone_number: null,
        guardian_telephone_number: null,
        guardian_address: null,
        guardian_province_id: null,
        guardian_city_id: null,
        guardian_district_id: null,
        guardian_village_id: null,
        guardian_rt: null,
        guardian_rw: null,
        guardian_postal: null,
        number_of_sibling: null,
        order_child: null,
      },
    };
  },
  computed: {
    ...mapGetters({
      getDataPendidikan: "pendidikan/datapendidikan",
      getDataPekerjaan: "pekerjaan/datapekerjaan",
      getDataPenghasilan: "penghasilan/datapenghasilan",
      getDataProvinsi: "provinsi/dataprovinsi",
      getDataFather: "ortu/datafather",
      getDataMother: "ortu/datamother",
      getDataGUardian: "ortu/dataguardian",
      getDataStudent: "ortu/datastudent",
      getDataKota: "kokab/datakokab",
      getDataKecamatan: "kecamatan/datakec",
      getDataDesa: "desakelurahan/datades",
    }),
  },
  methods: {
    ...mapActions({
      datapendidik: "pendidikan/pendidikan",
      datakerja: "pekerjaan/pekerjaan",
      datapenghasilan: "penghasilan/penghasilan",
      dtprovinsi: "provinsi/provinsi",
      dtkokab: "kokab/kokab",
      dtkecamatan: "kecamatan/kecamatan",
      dtdesa: "desakelurahan/desakelurahan",
      dtfather: "ortu/ortu",
      dtmother: "ortu/ortu",
      dtguardian: "ortu/ortu",
      dtstudent: "ortu/ortu",
    }),
    getDTPendidik() {
      this.datapendidik();
    },
    getDTPekerjaan() {
      this.datakerja();
    },
    getDTpenghasilan() {
      this.datapenghasilan();
    },
    getPilihProvinsi() {
      this.dtprovinsi();
    },
    getDTFather() {
      this.dtfather();
    },
    getDTMother() {
      this.dtmother();
    },
    getDTGuardian() {
      this.dtguardian();
    },
    getDTStudent() {
      this.dtstudent();
    },
    async submit() {
      try {
        let dataOrtu = {
          npm: this.$store.state.auth.users.username,
          father_full_name: this.form.father_full_name,
          father_date_of_birth: this.form.father_date_of_birth,
          father_education_level_id: this.form.father_education_level_id,
          father_job_field_id: this.form.father_job_field_id,
          father_salary_id: this.form.father_salary_id,
          father_handphone_number: this.form.father_handphone_number,
          father_telephone_number: this.form.father_telephone_number,
          father_address: this.form.father_address,
          father_province_id: this.form.father_province_id,
          father_city_id: this.form.father_city_id,
          father_district_id: this.form.father_district_id,
          father_village_id: this.form.father_village_id,
          father_rt: this.form.father_rt,
          father_rw: this.form.father_rw,
          father_postal: this.form.father_postal,
          mother_full_name: this.form.mother_full_name,
          mother_date_of_birth: this.form.mother_date_of_birth,
          mother_education_level_id: this.form.mother_education_level_id,
          mother_job_field_id: this.form.mother_job_field_id,
          mother_salary_id: this.form.mother_salary_id,
          mother_handphone_number: this.form.mother_handphone_number,
          mother_telephone_number: this.form.mother_telephone_number,
          mother_address: this.form.mother_address,
          mother_province_id: this.form.mother_province_id,
          mother_city_id: this.form.mother_city_id,
          mother_district_id: this.form.mother_district_id,
          mother_village_id: this.form.mother_village_id,
          mother_rt: this.form.mother_rt,
          mother_rw: this.form.mother_rw,
          mother_postal: this.form.mother_postal,
          guardian_full_name: this.form.guardian_full_name,
          guardian_date_of_birth: this.form.guardian_date_of_birth,
          guardian_education_level_id: this.form.guardian_education_level_id,
          guardian_job_field_id: this.form.guardian_job_field_id,
          guardian_salary_id: this.form.guardian_salary_id,
          guardian_handphone_number: this.form.guardian_handphone_number,
          guardian_telephone_number: this.form.guardian_telephone_number,
          guardian_address: this.form.guardian_address,
          guardian_province_id: this.form.guardian_province_id,
          guardian_city_id: this.form.guardian_city_id,
          guardian_district_id: this.form.guardian_district_id,
          guardian_village_id: this.form.guardian_village_id,
          guardian_rt: this.form.guardian_rt,
          guardian_rw: this.form.guardian_rw,
          guardian_postal: this.form.guardian_postal,
          number_of_sibling: this.form.number_of_sibling,
          order_child: this.form.order_child,
        };
        console.log(dataOrtu);
        let response = await axios.put("/oasis/parent", dataOrtu);

        if (response.data.meta.status === "success") {
          this.notyf.success({
            message: response.data.meta.message,
            dismissible: false,
            duration: 5000,
          });
        } else {
          this.notyf.error({
            message: response.data.meta.message,
            dismissible: false,
            duration: 5000,
          });
        }
        this.getDTPendidik();
        this.getDTPekerjaan();
        this.getDTpenghasilan();
        this.getPilihProvinsi();
        this.getDTFather();
        this.getDTMother();
        this.getDTGuardian();
        this.getDTStudent();
        // return response;
      } catch (error) {
        // console.warn(error);
        console.warn(error);
      }
    },
  },
  mounted() {
    this.getDTPendidik();
    this.getDTPekerjaan();
    this.getDTpenghasilan();
    this.getPilihProvinsi();
    this.getDTFather();
    this.getDTMother();
    this.getDTGuardian();
    this.getDTStudent();
    this.form.father_full_name = this.getDataFather.full_name;
    this.form.father_date_of_birth = this.getDataFather.date_of_birth;
    this.form.father_education_level_id = this.getDataFather.education_level_id
    this.form.father_job_field_id = this.getDataFather.job_field_id
    this.form.father_salary_id = this.getDataFather.salary_id
    this.form.mother_full_name = this.getDataMother.full_name;
    this.form.mother_education_level_id = this.getDataMother.education_level_id
    this.form.mother_date_of_birth = this.getDataMother.date_of_birth;
    this.form.mother_job_field_id = this.getDataMother.job_field_id
    this.form.mother_salary_id = this.getDataMother.salary_id
    this.form.number_of_sibling = this.getDataStudent.number_of_sibling;
    this.form.order_child = this.getDataStudent.order_child;
    this.form.guardian_full_name = this.getDataGUardian.full_name;
    this.form.guardian_job_field_id =
      this.getDataGUardian.job_field_id;
    this.form.guardian_address = this.getDataGUardian.address;
    this.form.guardian_rt = this.getDataGUardian.rt;
    this.form.guardian_rw = this.getDataGUardian.rw;
    this.form.guardian_handphone_number = this.getDataGUardian.handphone_number;
    this.form.guardian_telephone_number = this.getDataGUardian.telephone_number;
    this.form.guardian_province_id = this.getDataGUardian.province_id
  },
};
</script>

<style>
</style>